<template>
	<div class="news-list">
		<ul class="list" ref="newsList"
			v-infinite-scroll="loadMore"
			infinite-scroll-disabled="loading"
			infinite-scroll-immediate-check=false
			infinite-scroll-distance="40">
			<li class="list-item" v-for="story in this.$store.state.stories" :key="story.id" @click="viewDetail(story.id)">
				<span class="item-title">{{story.title}}</span>
				<div class="image-wrapper">
					<img class="item-image" v-lazy.newsList="attachImageUrl(story.images[0])" :alt="story.title">
					<i class="icon iconfont icon-duotu multipic" v-if="story.multipic"></i>
				</div>
			</li>
		</ul>
	</div>
</template>

<script>
	import axios from 'axios';
	import router from '../router';
	import { Indicator } from 'mint-ui';
	export default {
		data() {
			return {
				loading: false,
				date: Date,
				dateStr: '' // 
			};
		},
		created() {
			// 
			if (this.$store.state.isFirstLoad) {
				this.fetchData();
				this.$store.dispatch('changeLoadState');
			}
			this.initDate();

			// 
			this.$on('refresh', () => {
				this.refreshData();
			});
		},
		methods: {
			// id
      viewDetail: function(id) {
				this.$store.dispatch('changeCurrentNewsId', id);
				this.$store.dispatch('changeNewsType', 0);
        router.push({ name: 'newsDetail', params: { id: id } });
      },
			// 
      attachImageUrl: function(srcUrl) {
        if (srcUrl !== undefined) {
          return srcUrl.replace(/http\w{0,1}:\/\/p/g, 'https://images.weserv.nl/?url=p');
        }
      },
			// 
			fetchData: function() {
				axios.get('api/news/latest')
				.then(response => {
					// idstate
					let stories = response.data.stories;
					let ids = stories.map(story => story.id);

					this.$store.dispatch('addNews', {
						stories: stories,
						ids: ids
					});
				})
				.catch(error => {
					console.log(error);
				});
			},
			// 
			refreshData() {
				// 
				this.$store.dispatch('refreshNews');

				this.$nextTick(() => {
					this.fetchData();
				});
			},
			// 
			initDate: function() {
				this.date = new Date();
				this.changeDate2String();
			},
			// DateString
			changeDate2String: function() {
				let year = this.date.getFullYear();
				let month = this.date.getMonth() + 1;
				let day = this.date.getDate();
				month = month < 10 ? '0' + month : month; // 100
				day = day < 10 ? '0' + day : day; // 100;

				this.dateStr = year + month + day;
			},
			// 
			decreaseDate: function() {
				this.date.setDate(this.date.getDate() - 1);
				this.changeDate2String();
			},
			// 
			fetchMoreData: function() {
				axios.get('api/news/before/' + this.dateStr)
				.then(response => {
					// 
					let stories = response.data.stories;
					let ids = stories.map(story => story.id);

					this.$store.dispatch('addNews', {
						stories: stories,
						ids: ids
					});

					// 
					Indicator.close();
				})
				.catch(error => {
					console.log(error);
				});

				this.decreaseDate();
			},
			// 
			loadMore: function() {
				this.loading = true;

				// 
				Indicator.open({
					spinnerType: 'fading-circle'
				});

				// DOM
				this.$nextTick(function () {
					this.fetchMoreData();
				});

				this.loading = false;
			}
    }
	};
</script>

<style lang="less" scoped>
@import "../assets/less/components/NewsList.less";
</style>



